import React, {useEffect, useMemo, useState} from "react";
import {Html5Qrcode} from "html5-qrcode";
import {CameraDevice, QrcodeSuccessCallback} from "html5-qrcode/core";
import {IconButton} from "@mui/material";
import {Cameraswitch} from "@mui/icons-material";

type TProps = {
    onUpdate?: QrcodeSuccessCallback;
}

function QRCodeScanner ({onUpdate}: TProps) {
    const [devices, setDevices] = useState<Array<CameraDevice> | undefined>(undefined);
    const [devicesSelected, setDeviceSelected] = useState<number>(0);
    const [loading, setLoading] = useState<boolean>(true);
    useEffect(() => {
        if(devices) {
            var cameraId = devices[devicesSelected].id;
            const html5QrcodeScanner =  new Html5Qrcode("qr-reader");
            html5QrcodeScanner.start(
                cameraId,     // retreived in the previous step.
                {
                    fps: 10,    // sets the framerate to 10 frame per second
                    qrbox: 250  // sets only 250 X 250 region of viewfinder to
                                // scannable, rest shaded.
                },
                (qrCodeMessage, result) => {
                    onUpdate && onUpdate(qrCodeMessage, result);
                    // do something when code is read. For example:
                    console.log(`QR Code detected: ${qrCodeMessage}`);
                }, undefined)
                .catch(err => {
                    // Start failed, handle it. For example,
                    console.log(`Unable to start scanning, error: ${err}`);
                });
            return () => {
                html5QrcodeScanner.stop().then(ignore => {
                    // QR Code scanning is stopped.
                    console.log("QR Code scanning stopped.");
                }).catch(err => {
                    // Stop failed, handle it.
                    console.log("Unable to stop scanning.");
                });
            }
        }
    }, [devices, devicesSelected])
    useEffect(() => {
        if(!devices) Html5Qrcode.getCameras().then(devices => {
            /**
             * devices would be an array of objects of type:
             * { id: "id", label: "label" }
             */
            if (devices && devices.length) {
                setDevices(devices);
            }
        }).catch(err => {
            console.error(err);
        });
    })
    return (
        <div>
            <div id="qr-reader" style={{width: "80vw", maxWidth:"500px", minWidth:"250px"}}/>
            {devices && devices.length > 1 && <div style={{position: "absolute", top: 16, right: 16}}>
                <IconButton aria-label="delete" size="large" onClick={() => setDeviceSelected( devicesSelected === 0 ? 1 : 0)}>
                    <span className="fa-duotone fa-camera-rotate"/>
                </IconButton>
            </div>}
        </div>
    );
}

export default QRCodeScanner;