import {Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, TextField} from "@mui/material";
import React, {useState} from "react";
import BarcodeScanner from "./BarcodeScanner";
import QRCodeScanner from "./QRCodeScanner";


type TProps = {
    callback?: (code:string) => void
    onClose?: () => void,
}
function ModalBarcodeScanner (props:TProps) {
    const [code, setCode] = useState<string>("");
    return (
        <Dialog open={true}  onClose={() => props.onClose && props.onClose()}>
            <QRCodeScanner onUpdate={decodedText => setCode(decodedText)}/>
            <DialogContent>
                <TextField
                    value={code}
                    onChange={e => setCode(e.target.value ?? "")}
                    margin="dense"
                    id="name"
                    label="Barcode"
                    type="text"
                    fullWidth
                    variant="filled"
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={() => props.onClose && props.onClose()}>Cancel</Button>
            </DialogActions>
        </Dialog>
    )
}

export default ModalBarcodeScanner;